body {
  margin: 0;
  padding: 0px;
  background: -webkit-linear-gradient(left, rgba(203,212,196,1) 0%, rgb(25, 92, 97) 100%);
  background: linear-gradient(to right, rgba(203,212,196,1) 0%, rgb(25, 92, 97) 100%);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.general{
  width: 30%;
  min-height: 500px ;
  margin-left: 35%;
  margin-top: 60px;
  background-color: rgb(25, 92, 97);
  padding-top: 1px;
  text-align: center;
  color: #fff;
  border-radius: 10px;
  box-shadow: 10px 10px 34px -6px rgba(0,0,0,0.5);
}
input {
    width: 80%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    border-radius: 10px;
    color: rgb(25, 92, 97);
}

input:focus{
    outline: 0;
    outline-offset: 0;
}
button {
    background-color: rgb(255, 255, 255); /* Green */
    border: none;
    color: rgb(25, 92, 97);
    width: 80%;
    padding: 12px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    border-radius: 10px;
    margin-top: 10px;
}
button:focus{
    outline: 0;
    outline-offset: 0;
}
.result{
    width: 100%;
    height: 350px;
}
